"use client"

import { generateRandomString } from "@/lib/utils";
import { createContext, useContext, useEffect, useState } from "react";

interface CartContextProps{
    cartCode: string | null
    cartItemsCount: number
    clearCartCode: () => void
    setCartItemsCount: (curr: number) => void
}

const CartContext = createContext<CartContextProps | null>(null)

export function CartProvider({children}: {children: React.ReactNode}) {

    const [cartCode, setCartCode] = useState<string | null>(null)
    const [cartItemsCount, setCartItemsCount] = useState(0)

    useEffect(() => {

        let code = localStorage.getItem('cartCode')
        if(!code){
            code = generateRandomString()
            localStorage.setItem('cartCode', code)
        }
        setCartCode(code)
    }, [])

    function clearCartCode() {
        localStorage.removeItem('cartCode')
        setCartCode(null)
    }

    return (
        <CartContext.Provider 
        value={{cartCode, cartItemsCount, clearCartCode, setCartItemsCount}}
        >
            {children}

        </CartContext.Provider>
    )

}

export function useCart(){
    const context = useContext(CartContext)
    if(!context){
        throw new Error('useCart must be used within a CartProvider')
    }
    return context
}